/* Retro Browser Styles */
.browser-viewport div#home {
  background: #ffffff
    url('data:image/gif;base64,R0lGODlhAQABAIAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==');
  font-family: 'Times New Roman', serif;
}

.browser-viewport div#home h1 {
  text-align: center;
  color: #ff0000;
  font-family: 'Comic Sans MS', 'Arial', sans-serif;
  text-decoration: underline;
  animation: rainbow 3s linear infinite;
}

@keyframes rainbow {
  0% {
    color: #ff0000;
  }
  16% {
    color: #ff8800;
  }
  33% {
    color: #ffff00;
  }
  50% {
    color: #00ff00;
  }
  66% {
    color: #0088ff;
  }
  83% {
    color: #8800ff;
  }
  100% {
    color: #ff0000;
  }
}

.browser-viewport div#home .welcome {
  border: 3px ridge #c0c0c0;
  background: linear-gradient(135deg, #ffffcc, #ccffff);
  margin: 20px auto;
  text-align: center;
}

.browser-viewport div#home p {
  font-size: 14px;
  color: #000080;
}

/* Marquee Animation */
.marquee-container {
  overflow: hidden;
  white-space: nowrap;
  box-sizing: border-box;
  width: 100%;
  background: linear-gradient(90deg, #ffff00, #ff00ff, #00ffff, #ffff00);
  padding: 5px 0;
  margin: 10px 0;
}

.marquee-text {
  display: inline-block;
  padding-left: 100%;
  animation: marquee 15s linear infinite;
}

@keyframes marquee {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }
}

/* Retro NEW and HOT badges */
.retro-new-badge {
  display: inline-block;
  background: linear-gradient(45deg, #ff0000, #ffff00);
  color: #ffffff;
  font-weight: bold;
  font-size: 11px;
  padding: 2px 6px;
  margin: 0 5px;
  border: 2px solid #000000;
  border-radius: 4px;
  text-shadow: 1px 1px 2px #000000;
  animation: flash 1s linear infinite;
  vertical-align: middle;
}

@keyframes flash {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0.6;
  }
}

/* Under Construction */
.under-construction {
  text-align: center;
  margin: 15px 0;
  padding: 10px;
  background: repeating-linear-gradient(45deg, #ffff00, #ffff00 10px, #000000 10px, #000000 20px);
  border: 3px solid #ff0000;
}

.construction-text {
  color: #ff0000;
  font-weight: bold;
  font-size: 14px;
  text-shadow: 2px 2px 0px #ffffff;
  letter-spacing: 2px;
  animation: blink 1s step-start infinite;
}

.construction-icon {
  font-size: 20px;
  margin: 0 10px;
  animation: rotate-icon 2s linear infinite;
}

@keyframes rotate-icon {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes blink {
  0%,
  50% {
    opacity: 1;
  }
  51%,
  100% {
    opacity: 0;
  }
}

/* Override clip-path when inside browser window */
.browser-viewport .welcome {
  clip-path: none;
  background-color: #ffffcc;
  border: 2px dashed #ff00ff;
  padding: 15px;
  margin: 10px 0;
}

.browser-viewport .welcome.secondary {
  background-color: #ccffff;
  border: 2px dashed #0088ff;
}
